import React from "react";
import { useFormik } from "formik";
import * as yup from "yup";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { useNavigate } from "react-router-dom";
import AddMembers from "../AddMembers/AddMembers";

const SignUp = () => {
    const navigate = useNavigate();
  const validationSchema = yup.object({
    teamName: yup
      .string()
      .required("  ")
      .max(20, "  20 "),
    login: yup.string().required("    "),
    password: yup
      .string()
      .required("   ")
      .min(8, "   8 ")
      .matches(
        /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]+$/,
        "       ,   ,      "
      ),
    email: yup
      .string()
      .email("   ")
      .required("Email   "),

    confirmPassword: yup
      .string()
      .required(" ")
      .oneOf([yup.ref("password"), null], "  "),

    banner: yup
      .mixed()
      .required("   ")
      .test("fileSize", "   2 ", (value) => {
        return !value || value.size <= 2 * 1024 * 1024;
      })
      .test(
        "fileFormat",
        " : jpeg, png, pdf",
        (value) =>
          value &&
          ["image/jpeg", "image/png", "application/pdf"].includes(value.type)
      ),
      
  });

  const handleSubmit = (values) => {
    console.log(values);
    localStorage.setItem('AuthenticationStatus', true);
    navigate('/signin');
    //    
  };

  const formik = useFormik({
    initialValues: {
      teamName: "",
      login: "",
      password: "",
    },
    validationSchema: validationSchema,
    onSubmit: handleSubmit,
  });

  return (
    <Container component="main" maxWidth="xs" maxHeight="110vh">
      <CssBaseline />
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          minHeight: "80vh",
        }}
      >
        <Box
          sx={{
            mt: 2,
            mb: 2,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            border: "1px solid rgba(0, 0, 0, 0.4)", //  
            borderRadius: "16px", //  
            boxShadow: "0px 0px 7px rgba(0, 0, 0, 0.3)", // 
            padding: "20px", //    
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
             
          </Typography>
          <Box
            component="form"
            noValidate
            onSubmit={formik.handleSubmit}
            sx={{ mt: 3 }}
          >
            <TextField
              margin="normal"
              autoComplete="team-name"
              name="teamName"
              required
              fullWidth
              id="teamName"
              label=" "
              autoFocus
              value={formik.values.teamName}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.teamName && Boolean(formik.errors.teamName)}
              helperText={formik.touched.teamName && formik.errors.teamName}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              id="login"
              label=" "
              name="login"
              autoComplete="login"
              value={formik.values.login}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.login && Boolean(formik.errors.login)}
              helperText={formik.touched.login && formik.errors.login}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label=""
              type="password"
              id="password"
              autoComplete="new-password"
              value={formik.values.password}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.password && Boolean(formik.errors.password)}
              helperText={formik.touched.password && formik.errors.password}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="confirmPassword"
              label=" "
              type="password"
              id="confirmPassword"
              autoComplete="new-password"
              value={formik.values.confirmPassword}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={
                formik.touched.confirmPassword &&
                Boolean(formik.errors.confirmPassword)
              }
              helperText={
                formik.touched.confirmPassword && formik.errors.confirmPassword
              }
            />
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label=""
              name="email"
              autoComplete="email"
              value={formik.values.email}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.email && Boolean(formik.errors.email)}
              helperText={formik.touched.email && formik.errors.email}
            />

            <Box
              sx={{
                mt: 1,
                display: "flex",
                justifyContent: "left",
                alignItems: "center",
              }}
            >
              <Button
                variant="contained"
                component="label"
                sx={{ backgroundColor: "#9747FF", color: "white" }}
                onBlur={formik.handleBlur("banner")}
              >
                 
                <input
                  type="file"
                  hidden
                  onChange={(event) => formik.setFieldValue("banner", event.currentTarget.files[0])}

                />
              </Button>
              {formik.touched.banner && formik.errors.banner && (
                <Box sx={{ color: "red", marginLeft: 1, fontSize: '12px' }}>{formik.errors.banner}</Box>
)}
            </Box>
              <br/>
              <AddMembers/>

            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{
                mt: 3,
                mb: 2,
                backgroundColor: "#9747FF",
                color: "white",
              }}
              disabled={!formik.isValid}
            >
              
            </Button>
            <Grid container justifyContent="flex-end">
              <Grid item>
                <Link
                  href="/signin"
                  variant="body2"
                  sx={{ color: "#9747FF", textDecoration: "none" }}
                >
                    ? 
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Box>
    </Container>
  );
};

export default SignUp;
